// JS Imports
import React, {Component} from 'react';
import MDC from './plugins/MaterialUIComponents' // Material-UI component kit
import Dash from '@material-ui/icons/ArrowForwardIosSharp' // Material-UI component kit
import getComputedStyles from './plugins/GetComputedStyles'

import styleTag from './plugins/HTMLStyleTag'; // In-component style tag plugin
// CSS Imports
import './layout/App.css';
import './layout/normalizer.css'
import './layout/theme.css'

// others
import logo from './assets/icons/interface/png/user.png'
import aim from './assets/images/aim.png'
import target from './assets/images/target.png'




class App extends Component {
  constructor(props) {
    super(props)

    this.mobile = ''
    this.windowWidth = window.innerWidth
    this.windowHeight = window.innerHeight
    this.topHeader = ''
    this.headerHeight = ''
    this.pageHeight = ''
  }
  componentDidMount() {
    styleTag() // this activates the use of HTML style tags for in-component styling

    window.innerwidth < window.innerHeight
    ? this.mobile = true
    : this.mobile = true

    this.topHeader = document.getElementById('top-header')

    this.headerHeight = getComputedStyles(this.topHeader, 'height')

    this.pageHeight = this.windowHeight - this.headerHeight
    this.forceUpdate()
  }

  render() {
    return (
      <div className="App">
        <MDC.Box
          minHeight={this.pageHeight}
          className="blue"
          mt="4rem"
          pt={{xs:'0px', lg:"4rem"}}
        >
          <MDC.AppBar
            id="top-header"
          >
            <MDC.Box
              className="blue"
              display='flex'
              elevation={5}
              justifyContent='space-between'
              height='4rem'
              px='0.5vmin'
              py='0.5rem'
            >
              <MDC.Box
                display='flex'
                justifyContent='center'
                width={{xs:'15%', lg:'5%'}}
              >
                <MDC.Avatar
                  height='100%'
                  width='100%'
                  vertical-align='center'
                  mx='0px'
                  my='0px'
                  className='transparent'
                >
                  <img src={logo} width='100%' alt='logo' cover />
                </MDC.Avatar>
              </MDC.Box>
              

              <h3 className='yellow--text'>
                {`<Peter Collins />`}
              </h3>

              <MDC.Box
                width={{xs:'15%', lg:'5%'}}
              >
              </MDC.Box>
            </MDC.Box>
          </MDC.AppBar>
          

          <MDC.Box
            height={{xs:'55vh', lg:'50vh'}}
            align='center'
          >
            <MDC.Box
              height={{xs:'55vh', lg:'50vh'}}
              width={{xs:'100%', lg:'30%'}}
              py='20vh'
              id='blur'
            >
              <img src={aim} alt='Aim' height='120rem' width='120rem' />
            </MDC.Box>

            <MDC.Box
              height={{xs:'55vh', lg:'50vh'}}
              width={{xs:'100%', lg:'30%'}}
              mx='auto'
              top={{xs:'10vh', lg:'15vh'}}
              className='target'
            >
              <img src={target} alt='Target' width='100%' height='110%' />
            </MDC.Box>
          </MDC.Box>

          <MDC.Box
            p='0.5vmin'
            mt='0.5rem'
          >
            <MDC.Box
              width={{xs:'70%', lg:'30%'}}
              mx='auto'
            >
              <h2 className='yellow blue--text'>My Aim is Clear</h2>
            
              <p className='lightblue--text' align='left'>
                To help you build a strong online presence for your brand,
                removing all obstacles so you can achieve your objectives.
              </p>

              <MDC.Button className='lightblue--text' fontSize='5rem'>
                <Dash className='dash1 yellow--text' />
                <Dash className='dash2 yellow--text' />
                <Dash className='dash3 yellow--text' />
              </MDC.Button>
            </MDC.Box>
          </MDC.Box>
        </MDC.Box>

          
        <style scoped>
          {`
            .full-height {height: 100% !important}

            #blur {
              position: relative;
              background: radial-gradient(circle,   transparent, rgba(145, 31, 30, 0.75));
              animation: fade-in 10s;
              z-index: 2 !important;
            }

            img[alt = 'Aim'] {
              position: relative;
              animation: move-aim 10s;
              animation-iteration-count: infinite;
              animation-timing-function: ease-in-out;
            }

            .target {
              position: relative;
              transform: translateY(-65vh);
            }
            
            
            @keyframes fade-in {
              from {opacity: 0;}
              to {opacity: 1;}
            }

          `}
        </style>
      </div>
    );
  }
}

export default App;
